* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
  background-color: #000;
  color: #fff;
}

.container {
  margin: 0 auto;
  max-width: 930px;
  width: 100%;
  padding: 0 15px;
}

/** HEADER **/
.header {
  background-image: url("../images/bg.jpeg");
  background-size: cover;
  background-position: center bottom;
}

.header .container {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 520px;
}

.header h1 {
  font-size: 60px;
  font-weight: 700;
  text-transform: uppercase;
}

/** BOXES **/
.boxes {
  background-color: #11203e;
  padding: 60px 0;
}

.boxes .container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}

.boxes .box {
  text-align: center;
  padding: 20px;
  width: 33.333%;
  min-width: 250px;
}

.boxes .box-title {
  font-weight: 600;
  font-size: 27px;
  margin: 30px 0;
}

.boxes .box-content {
  font-weight: 300;
  font-size: 0.9em;
  line-height: 1.2em;
  color: #b7b7b7;
}

.boxes .box-btn {
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  display: inline-block;
  padding: 8px 36px;
  border: 1px solid #fff;
  border-radius: 20px;
  margin-top: 30px;
}

/** FOOTER **/
footer {
  background-color: #000;
}

footer .container {
  height: 125px;
  display: flex;
  align-items: center;
  justify-content: center;
}

footer p {
  color: #b7b7b7;
}
